*, *:after, *:before {box-sizing:border-box;text-size-adjust:none;-webkit-tap-highlight-color:rgba(255, 255, 255, 0);-webkit-font-smoothing:antialiased;} 
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;word-break:keep-all;word-wrap:break-word;white-space:normal;line-height:1;}
article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block;}
ol,ul{list-style:none;}
table{border-collapse:collapse;border-spacing:0;table-layout:fixed;}
audio,embed,iframe,img,object,video{max-width:100%;}
img{height:auto;vertical-align:top;}
address,i{font-style:normal;}
em{font-style:inherit;}
a,s{text-decoration:none;color:inherit;}
b,h1,h2,h3,h4,h5,strong,th{font-weight:400;}
:focus{outline:0;}
button,select{text-transform:none;font-family:inherit;font-size:inherit;letter-spacing:inherit;line-height:inherit;}
button{cursor:pointer;box-sizing:border-box;border: 0;}
form button,input,optgroup,select,textarea{appearance:none;-moz-appearance:none;margin:0;padding:0;background-color:transparent;border-radius:0;border:0;font-family:inherit;font-size:inherit;letter-spacing:inherit;line-height:inherit;}
select{ overflow:hidden; white-space:nowrap; text-align:left; }
input::-ms-clear{display:none;}
select::-ms-expand {display:none;}
body,html{
    font-family: 'SUIT',Dotum,Gulim,Sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.4;
    letter-spacing: -.5px;
    color: #333;
    -webkit-overflow-scrolling: touch
}